@import '../node_modules/animate.css/source/fading_entrances/fadeIn.css';
@import '../node_modules/animate.css/source/fading_entrances/fadeInDown.css';
@import '../node_modules/animate.css/source/fading_entrances/fadeInRight.css';
@import '~animate.css';
/* Section */
body {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow: hidden;
}

.section-scroller .section {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin-top: 269px;
}

.section-scroller .section {
  display: none;
}
.section-scroller .section.active {
  display: block;
}
.section-scroller .column .column-wrapper {
  animation-duration: 1.5s;
}
.section-scroller .column:nth-child(1) .column-wrapper {
  animation-delay: 0s;
}
.section-scroller .column:nth-child(2) .column-wrapper {
  animation-delay: 0.3s;
}
.section-scroller .column:nth-child(3) .column-wrapper {
  animation-delay: 0.6s;
}
.section-scroller .column:nth-child(4) .column-wrapper {
  animation-delay: 0.9s;
}
.section-scroller .second-section {
  margin-top: 0 !important;
}
.section-scroller .second-section .small-logo {
  position: relative;
  z-index: 2;
  min-height: 290px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.section-scroller .second-section .small-logo .small-logo-warpper {
  cursor: pointer;
}
.section-scroller .second-section .content-section {
  padding: 0 120px;
}

.page-wrapper {
  height: 100vh;
}

.section-wrapper {
  height: 100%;
}

.hero-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  margin-top: -133px;
  padding-bottom: 0;
}
.hero-wrapper .top-section {
  animation-duration: 1.5s;
}
.hero-wrapper .top-section .logo {
  margin-bottom: 70px;
}
.hero-wrapper .top-section .welcome {
  font-size: 16px;
  max-width: 390px;
}
.hero-wrapper .bottom-section {
  position: absolute;
  bottom: -95px;
  animation-duration: 1.5s;
}
.hero-wrapper .bottom-section .mouse {
  margin-bottom: 15px;
}

.iScrollVerticalScrollbar {
  display: none;
}

.main-menu-wrwapper {
  display: flex;
  flex-direction: column;
  position: relative;
}
.main-menu-wrwapper .logo-section {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 266px;
}
.main-menu-wrwapper .brief-button {
  position: absolute;
  left: calc(50% - (246px / 2));
  bottom: 15%;
}
.main-menu-wrwapper .content-section {
  padding-top: 30px;
  display: flex;
  flex-direction: column;
  flex: 1;
}
.main-menu-wrwapper .content-section a {
  text-decoration: none;
}
.main-menu-wrwapper .row {
  --bs-gutter-x: 0;
  margin-right: 80px;
  flex: 1;
}
.main-menu-wrwapper .column-wrapper {
  padding: 0 20px;
  position: relative;
  height: 100%;
}
.main-menu-wrwapper .column-wrapper .title {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 20px;
}
.main-menu-wrwapper .column-wrapper .description,
.main-menu-wrwapper .column-wrapper .look-here {
  line-height: 1.5;
  margin-bottom: 20px;
}
.main-menu-wrwapper .column-wrapper .description.letter-spacing,
.main-menu-wrwapper .column-wrapper .look-here.letter-spacing {
  letter-spacing: -1px;
}
.main-menu-wrwapper .column-wrapper .encrypted {
  position: relative;
  transition: all 0.3s ease-in-out;
  height: 100%;
}
.main-menu-wrwapper .column-wrapper .encrypted::before {
  content: "";
  position: absolute;
  left: -20px;
  right: -20px;
  top: -300px;
  bottom: 0;
  background: linear-gradient(179.86deg, #000000 28.19%, rgba(0, 0, 0, 0) 91.01%);
  border-style: solid;
  border-left-width: 1px;
  border-right-width: 1px;
  border-image: linear-gradient(to bottom, #fff, rgba(0, 0, 0, 0)) 1 100%;
  z-index: -1;
  opacity: 0;
  transition: all 0.3s ease-in-out;
}
.main-menu-wrwapper .column-wrapper .encrypted:hover::before {
  opacity: 1;
}
.main-menu-wrwapper .solar-system-wrapper {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  position: absolute;
  top: 0;
  bottom: 0;
  right: -120px;
  left: -120px;
  z-index: -1;
  animation-delay: 0.9s;
  animation-duration: 1s;
}
.main-menu-wrwapper .solar-system-wrapper #solar-system {
  width: 100%;
  transform: translate(-120px, 0);
}

@media (min-width: 1440px) {
  .main-menu-wrwapper .solar-system-wrapper #solar-system {
    transform: translate(-120px, 160px);
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  header {
    padding-bottom: 45px !important;
  }

  .section-scroller .second-section .small-logo {
    min-height: 180px !important;
  }
  .section-scroller .second-section .small-logo-warpper {
    margin-bottom: -30px !important;
  }
}
@media (max-width: 1199px) {
  .section-scroller .section {
    margin-top: 206px;
  }
  .section-scroller .second-section .small-logo {
    min-height: 210px;
  }
  .section-scroller .second-section .content-section {
    padding: 0 40px;
  }

  .hero-wrapper {
    margin-top: -103px;
  }
  .hero-wrapper .top-section .logo {
    margin-bottom: 40px;
  }
  .hero-wrapper .bottom-section {
    bottom: -65px;
  }
}
@media (max-width: 991px) {
  .second-section .content-section {
    overflow-y: auto;
    overflow-x: hidden;
  }

  .main-menu-wrwapper .brief-button {
    bottom: 5%;
  }
  .main-menu-wrwapper .logo-section {
    min-height: 206px;
  }
  .main-menu-wrwapper .content-section {
    padding-top: 0;
  }
  .main-menu-wrwapper .content-section .row {
    flex: unset;
    margin: 0 -40px;
    overflow-x: hidden;
    overflow-y: auto;
  }
  .main-menu-wrwapper .column {
    margin-bottom: 25px;
  }
  .main-menu-wrwapper .column-wrapper {
    padding: 0;
    position: relative;
  }
  .main-menu-wrwapper .column-wrapper::after {
    content: "";
    position: absolute;
    left: 150px;
    top: 0;
    right: 0;
    bottom: 0;
    border-top-width: 1px;
    border-bottom-width: 1px;
    border-top-style: solid;
    border-bottom-style: solid;
    border-image: linear-gradient(to left, #fff, rgba(0, 0, 0, 0)) 100% 1;
    background: linear-gradient(-90deg, #000000 0%, rgba(0, 0, 0, 0) 100%);
    z-index: -1;
  }
  .main-menu-wrwapper .column-wrapper .encrypted {
    padding: 15px 0;
    margin-left: auto;
    max-width: 50%;
    margin-right: 30px;
  }
  .main-menu-wrwapper .column-wrapper .encrypted::before {
    display: none;
  }
  .main-menu-wrwapper .column-wrapper .title {
    margin-bottom: 10px;
  }
  .main-menu-wrwapper .column-wrapper .description {
    margin-bottom: 10px;
  }
  .main-menu-wrwapper .solar-system-wrapper {
    right: 0;
    left: 0;
  }
  .main-menu-wrwapper .solar-system-wrapper #solar-system {
    width: auto;
    transform: none;
    position: absolute;
    left: -75%;
    right: -20%;
    bottom: 32%;
  }
  .main-menu-wrwapper .solar-system-wrapper #solar-system svg {
    transform: translate3d(0px, 0px, 0px) rotate3d(1, 1, 0, -180deg) !important;
  }
}
@media (max-width: 767px) {
  .brief-button {
    font-size: 14px;
    width: 200px;
    height: 45px;
  }

  .section-scroller .section {
    margin-top: 136px;
  }
  .section-scroller .second-section .small-logo {
    min-height: 136px;
  }

  .hero-wrapper {
    margin-top: -52px;
  }
  .hero-wrapper .top-section {
    margin-bottom: 50px;
  }
  .hero-wrapper .top-section .logo {
    width: 140px;
    margin-bottom: 20px;
  }
  .hero-wrapper .top-section .welcome {
    font-size: 12px;
    padding: 0 20px;
  }
  .hero-wrapper .bottom-section {
    bottom: -25px;
  }
  .hero-wrapper .bottom-section .mouse {
    width: 20px;
    margin-bottom: 5px;
  }

  .main-menu-wrwapper .brief-button {
    left: calc(50% - (200px /2));
  }
  .main-menu-wrwapper .logo-section {
    display: none;
  }
  .main-menu-wrwapper .column {
    margin-bottom: 15px;
  }
  .main-menu-wrwapper .column-wrapper::after {
    left: 0;
  }
  .main-menu-wrwapper .column-wrapper .encrypted {
    max-width: 100%;
    margin-left: 25%;
    margin-right: 20px;
  }
  .main-menu-wrwapper .column-wrapper .title {
    font-size: 14px;
  }
  .main-menu-wrwapper .column-wrapper .description {
    font-size: 10px;
  }
  .main-menu-wrwapper #solar-system {
    left: -120%;
    right: -50%;
    bottom: 35%;
  }
}
@media (max-width: 575px) {
  .main-menu-wrwapper .brief-button {
    display: none;
  }

  .section-scroller .second-section {
    margin-top: 82px !important;
  }
  .section-scroller .second-section .small-logo {
    visibility: hidden;
    position: absolute;
  }
}
@media (max-height: 670px) {
  .main-menu-wrwapper .column-wrapper .encrypted {
    padding: 10px 0;
    margin-left: 15px;
    margin-right: 15px;
  }
  .main-menu-wrwapper .column-wrapper .title {
    font-size: 12px;
    margin-bottom: 5px;
  }
  .main-menu-wrwapper .column-wrapper .description {
    margin-bottom: 5px;
  }
  .main-menu-wrwapper .column-wrapper .look-here {
    font-size: 10px;
  }
}
.hero-section {
  display: flex;
  justify-content: center;
  text-align: center;
}

.header-logo {
  opacity: 0;
}

@media (max-width: 1199px) {
  .header-logo {
    top: 0;
  }
}
@media (max-width: 767px) {
  .header-logo {
    display: none !important;
  }
}